import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, OnDestroy, ViewChildren } from '@angular/core';
import { DataService } from 'src/app/common/data.service';
import { Observable, Subscription, fromEvent } from 'rxjs';
import { Pair } from 'src/app/model/pair.interface';
import { Éµangular_packages_platform_browser_platform_browser_d } from '@angular/platform-browser';

@Component({
  selector: 'app-game1',
  templateUrl: './game1.component.html',
  styleUrls: ['./game1.component.css']
})
export class Game1Component implements OnInit, AfterViewInit, OnDestroy {
  
  private pairs$: Observable<Pair[]>;
  private _aId =  -1;
  private _bId = -1;
  private _match = false;
  private _noMatch = false;
  private _matchsCnt = 0;
  private _elapsed = 0;
  private _elapsedInterval;


  public get elapsed(){
    return this._elapsed;
  }
  
  public get match (){
    return this._match;
  }
  public get noMatch() {
    return this._noMatch;
  } 


  
  constructor(private dataService: DataService) { 
  }
  ngOnInit() {
    this.pairs$ = this.dataService.observable$;
    this._elapsedInterval = setInterval( _ => this._elapsed++, 1000);
  }

  ngAfterViewInit(): void {
  }

  ngOnDestroy(): void {
  }

  public handleA(pair: Pair){
    this.handleClick(pair);
  }

  public handleB(pair: Pair){
    this.handleClick(undefined, pair);
  }
  
  public isItemASelected(id){
    return this._aId === id;
  }
  public isItemBSelected(id){
    return this._bId === id;
  }

  private handleClick(a:Pair = undefined, b:Pair = undefined){
    if(a){
      if(this._aId === -1  || this._aId !== a.id ){
        this._aId = a.id;        
      } else {
        this._aId = -1;
      }
    }
    if(b){
      if(this._bId === -1 || this._bId !== b.id ){
        this._bId = b.id;        
      } else {
        this._bId = -1;
      }
    }

    this.checkMatch(a || b)

  }

  
  private checkMatch(pair: Pair) {
    if(this._aId !== -1 && this._aId === this._bId){
      pair.match = true;
      this._match = true;
      setTimeout( ()=> this._match = false, 250);
      this._aId = -1;
      this._bId = -1;
      this._matchsCnt++;
      if(this._matchsCnt === 5){
        clearInterval(this._elapsedInterval);
        setTimeout( _ => {
          this.dataService.next();
          this._matchsCnt = 0;
          this._elapsed = 0;
          this._elapsedInterval = setInterval( _ => this._elapsed++, 1000);
        }, 3000);
      }
    } else if((this._aId !== -1) && (this._bId !== -1) && (this._aId !== this._bId)){
      this._noMatch = true;
      setTimeout( ()=> this._noMatch = false, 250);
      this._aId = -1;
      this._bId = -1;
    }

    
  }


}
